<template>
  <div>
    <div class="box">
    <router-link to="/mine/registry" class="reg">registry</router-link>
    <router-link to="/mine/login" class="log">login</router-link>
    
  </div>
  <div>
    <router-view></router-view> 
  </div>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  mounted() {
    this.$eventbus.$emit("setTitle", "我的");
  }
};
</script>

<style lang="scss" scoped>
.box{
  display: flex;
  justify-content: space-between;
}
.reg,.log{
  color: rgb(43, 186, 211);
  margin: 10px;
  font-size: 0.5rem;
  text-decoration: none;
}
</style>



