<template>
    <div>
        <transition enter-active-class="animated fadeIn" leave-active-class="animated fadeOut">
            <div class="msgBox" v-if="show"></div>
        </transition>
        <transition enter-active-class="animated fadeIn" leave-active-class="animated fadeOut">
            <div class="msgBox2" v-if="show">{{}}</div>
        </transition>
    </div>
</template>

<script>
export default {
    data(){
        return{
            show:false
        }
    }
}
</script>

<style leng="scss">
.msgBox{
    position:fixed;
    top: 1rem;
    bottom: 1rem;
    left: 0;
    right: 0;
    background: #fff;
}
.msgBox{
    position:fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    margin: auto;
    width: 3rem;
    height: 1rem;
    border: 1px solid #000;
}
</style>



