import Vue from "vue";
const lazy = {
    arr:[],
    show (h,y){
        var arr = lazy.arr;
        var len = arr.length;
       arr.forEach(ele => {
            var eleTop = ele.el.offsetTop;
            var boxH = h;
            if(eleTop <boxH-y){
                ele.el.src = ele.value;
                ele.state =1;
            }
        })
        lazy.del();
    },
    del(){
        lazy.arr = lazy.arr.filter(ele=>ele.state===0)
    }
}
Vue.directive("lazyload",{
    bind(el,b,vnode){
        lazy.arr.push({
            el,
            value:b.value,
            state:0
        })
    }
})
export default lazy;


