import Vue from "vue";
import VueRouter from "vue-router";
import Index from "../pages/index";
import Goods from "../pages/goods";
import Buycar from "../pages/buycar";
import Mine from "../pages/mine";
import Detail from "../pages/goods/detail";
import Login from "../pages/mine/Login";
import Registry from "../pages/mine/Registry"
Vue.use(VueRouter);
const routes = [
    {
        path: "/",
        component: Index
    },
    {
        path : "/goods/",
        component : {
            template:`<div>
                请先选择商品分类  <router-link to="/goods2">命名</router-link> <br>
                <router-view name="a"></router-view>
                <router-view name="b"></router-view>
            </div>`,
            mounted(){
                this.$eventbus.$emit("setTitle", "商城")
            },
        },
        
        children:[
            {
                path:"/goods2",
                components:{
                    a : {
                        template:`<div>服饰</div>`
                    },
                    b : {
                        template:`<div>水果</div>`
                    }
                }
            }
        ]
    },
    {
        path : "/goods/:type",
        component : Goods
    },
    {
        path: "/buycar/",
        component: Buycar
    },
    {
        path: "/mine/",
        component: Mine,
        children:[
            {
                path: "/mine/Login/",
                component: Login
            },
            {
                path: "/mine/Registry/",
                component: Registry
            }
        ]
    },
    {
        path: "/detail/",
        component: Detail
    },
   
   
]
const router = new VueRouter({
    routes,
    mode: "history"
})
export default router;


