<template>
  <div>
    {{fn2()}}
    <div class="content">
      <p class="fan">
        <span class="fa fa-reply" onclick="window.history.back()" v-show="flag">返回</span>
      </p>
      <p>{{title}}</p>
      <span class="fa fa-bars" @click="fn">菜单</span>
    </div>
    <transition
      enter-class
      enter-active-class="animated fadeIn"
      enter-to-class
      leave-class
      leave-active-class="animated fadeOut"
      leave-to-class
    >
      <div class="mbox" v-if="msg" @click="fn"></div>
    </transition>
    <transition
      enter-class
      enter-active-class="animated fadeInRight"
      enter-to-class
      leave-class
      leave-active-class="animated fadeOutRight"
      leave-to-class
    >
      <div class="menu" v-if="msg">
        <li>0</li>
        <li>1</li>
        <li>2</li>
        <li>3</li>
        <li>4</li>
        <li>5</li>
        <li>6</li>
        <li>7</li>
        <li>8</li>
        <li>9</li>
      </div>
    </transition>
  </div>
</template>

<script>
import "../../assets/style/animate.min.css";
export default {
  data() {
    return {
      msg: false,
      flag: true,
      title: "天界"
    };
  },
  mounted() {
    // console.log(this.$route.path);
    this.$eventbus.$on(
      "setTitle",
      function(newTitle) {
        this.title = newTitle;
      }.bind(this)
    );
  },
  methods: {
    fn() {
      this.msg = !this.msg;
    },
    fn2() {
      if (this.$route.path === "/") {
        this.flag = false;
      } else {
        this.flag = true;
      }
    }
  }
};
</script>

<style lang="scss">
.content {
  height: 1rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: skyblue;
}
.content span {
  color: white;
  padding: 0 5px;
}
p {
  font-weight: 600;
  color: rgb(0, 119, 255);
}
.mbox {
  width: 100%;
  // height: 85%;
  position: fixed;
  right: 0;
  top: 1rem;
  bottom: 1rem;
  background: rgba(94, 184, 219, 0.5);
  color: white;
  z-index: 10;
}
.menu {
  width: 30%;
  // height: 100%;
  position: fixed;
  right: 0;
  top: 1rem;
  bottom: 1rem;
  background: rgb(113, 190, 221);
  color: white;
  z-index: 10;
}
.menu li {
  height: 10%;
  text-align: center;
  line-height: 1rem;
  // border-bottom: 1px solid #fff;
}
.menu li:hover {
  background: #fff;
  color: #000;
}
.fan {
  width: 1.2rem;
}
</style>



