<template>
    <transition
        enter-active-class="animated fadeIn"
        leave-active-class="animated fadeOut"
    >
        <div class="toast" v-if="show">{{txt}}</div>
    </transition>
</template>

<script>
export default {
    data(){
        return {
            txt : "",
            show : false
        }
    }
}
</script>

<style lang="scss" scoped>
.toast{
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    background:rgba(0,0,0,.8);
    color: white;
    padding: 10px;
    border-radius: 5px;
}
</style>



