import Toast from "./Toast.vue";
const toast = {};
toast.install = function(Vue,options){
    const Comp = Vue.extend(Toast);
    const vm = new Comp().$mount();
    document.body.appendChild(vm.$el);


    Vue.prototype.$toast = function(text){
        vm.show = true;
        vm.txt = text;
        setTimeout(()=>{
            vm.show=false
        },1500)
    }
}
export default toast;


