<template>
  <div  class="buycar">
    <h3>购物车列表</h3>
    <ul>
      <li v-for="(item,ind) of goodslist" :key="ind">
        <h2>{{item.title}}</h2>
        <div>
          <img :src="host+'/uploads/'+item.img">
          <p>
            <span>价格:{{item.price+"元"}}</span>

            <span>上架时间:{{item.addtime}}</span>
          </p>
          
           
        </div>
        <p class="btn">
           <button @click="update(item, -1)">
              <font class="fa fa-minus-square-o"></font>
            </button>
           <span> {{ item.num }}</span>
            <button @click="update(item, 1)">
              <font class="fa fa-plus-square-o"></font>
            </button>
        </p>
          
      </li>
    </ul>
  </div>
</template>

<script>
import { host } from "../../const";
import { mapActions } from 'vuex';
export default {
  data() {
    return {
      host: host
    };
  },
  mounted() {
    this.$eventbus.$emit("setTitle", "购物车");
  
  },
  computed: {
    goodslist() {
      return this.$store.state.goodslist;
    }
  },
  methods: {
    update(item,n){
      item.n = n;
      this.addCart(item)
  },
    ...mapActions(["addCart"])
  }
};
</script>

<style lang="scss" scoped>
.buycar{
    display: flex;
    flex-direction: column;
}
.buycar>ul{
    background:  rgb(28, 79, 99);
    flex: 1;
    overflow-y: scroll;
}
h3{
  display: block;
  text-align: center;
  height: 1rem;
  line-height: 1rem;
  background: rgb(28, 79, 99);
  color: orange
}
h2{
  color: orangered
}
li {
  height: 3rem;
  padding: 30px;
  margin: 5px;
  border: 1px solid #fff;
  color: #dad;
}
li div {
  display: flex;
  justify-content: space-around;
}
li div p {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}
:focus{
  outline: none
}
img {
  width: 2rem;
  height: 2rem;
  border-radius: 5px;
  border:1px solid #bbb;
  margin-top: 10px;
}
button{
  border: none;
  width: 0.3rem;
  height: 0.3rem;
  font-size: 0.3rem;
  background: none;
  color: #f0f0f0
}
span{
  color: #afc456
}
.btn{
  margin-left: .6rem;
  margin-top: 5px;
}
</style>



