<template>
  <div class="detail">
      <h1>{{title}}</h1>
      <img :src="img">
     <div>
          商品详情: <b v-html="content"></b>
     </div>
      
      <p>价格:{{this.price}}</p>
      商家:<span>{{name}}</span>
  </div>
</template>

<script>
import {host} from "../../const";
export default {
    data(){
        return {
            title:"",
            img:"",
            content:"",
            name:"",
            price:""
        }
    },
    mounted(){
        let {id} = this.$route.query;
        
        let url = host+"/goods?_type=_id&_word="+id;
        this.$axios.get(url).then(res=>{
            var obj = res.data.result[0];
            this.title = obj.titlel;
            this.img = host+"/uploads/"+obj.img;
            this.content = obj.content;
            this.price=obj.price;
            this.name=obj.username;
            console.log(obj)
        })
    }
};
</script>

<style scoped>
h1{
    font-size: 30px;
}
img{
    width: 4rem;
    height: 4rem;
}
</style>



