<template>
  <div>
    <h1>登录</h1>
    <div class="box">
      <form @submit.prevent="login">
        <p>
          <label>
            <span>用户名:</span>
            <input type="text" v-model="username.val" @blur="chkuser">
            <transition
              enter-class
              enter-active-class="animated fadeIn"
              enter-to-class
              leave-class
              leave-active-class="animated fadeOut"
              leave-to-class
            >
              <div class="msg" v-if="flag">{{username.msg}}</div>
            </transition>
          </label>
        </p>
        <p>
          <label>
            <span>密码:</span>
            <input type="text" v-model="password.val" @blur="chkupwd">
            <transition
              enter-class
              enter-active-class="animated fadeIn"
              enter-to-class
              leave-class
              leave-active-class="animated fadeOut"
              leave-to-class
            >
              <div class="msg" v-if="flag1">{{password.msg}}</div>
            </transition>
          </label>
        </p>
        <p class="btn">
          <button type="submit">Login</button>
        </p>
        <transition
          enter-class
          enter-active-class="animated fadeIn"
          enter-to-class
          leave-class
          leave-active-class="animated fadeOut"
          leave-to-class
        >
          <div class="msg" v-if="flag2">{{msg}}</div>
        </transition>
      </form>
    </div>
    <!-- <a @click="fn3"></a> -->
  </div>
</template>

<script scoped>
import { host } from "../../const";
export default {
  data() {
    return {
      username: {
        val: "",
        msg: ""
      },
      password: {
        val: "",
        msg: ""
      },
      msg: "",
      flag: false,
      flag1: false,
      flag2: false
    };
  },
  methods: {
    chkuser() {
      if (this.username.val == "") {
        this.username.msg = "用户名不能为空";
        this.flag = true;
        setTimeout(() => {
          this.flag = false;
        }, 1500);
      } else {
        this.username.msg = "";
      }
    },
    chkupwd() {
      if (this.password.val == "") {
        this.password.msg = "密码不能为空";
        this.flag1 = true;
        setTimeout(() => {
          this.flag1 = false;
        }, 1500);
      } else {
        this.password.msg = "";
      }
    },
    login() {
      if (this.username.val !== "" && this.password.val !== "") {
        let url = host + "/login";
        let data =
          "username=" + this.username.val + "&password=" + this.password.val;
        this.$axios.post(url, data).then(res => {
          if (res.data.text === "登录成功") {
            this.$router.push({
              path: "/"
            });
          } else {
            this.msg = res.data.text;
            this.flag2 = true;
            setTimeout(() => {
              this.flag2 = false;
            }, 1500);
          }
        });
      }
    }
  }
};
</script>


<style lang="scss" scoped>
h1 {
  text-align: center;
}

form p{
  margin:0 30%;
}

input {
  margin: 10px;
  height: 0.5rem;
  border: 1px solid rgb(58, 161, 202);
}

button {
  width: 4rem;
  height: .8rem;
  line-height: .8rem;
  border: none;
  background: orange;
  color: white;
  font-size: .3rem;
  margin-left: -10%;
}
.msg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 3rem;
  height: 1rem;
  line-height: 1rem;
  text-align: center;
  background: skyblue;
  color: red;
  border-radius: 10px;
  padding: 10px;
}
</style>



