<template>
  <li class="item">
    <router-link :to="'/detail?id='+item._id">
      <p>
        {{item.title}}
        <img :src="initimg" v-lazyload="host+'/uploads/'+item.img">
        <br>
      </p>
      <b>单价：{{item.price}}</b>
      <mark>查看详情</mark>
      
    </router-link>
    <p>
        <button @click="add(item)">
          <font class="fa fa-shoppong-cart"></font>添加到购物车
        </button>
      </p>
  </li>
</template>

<script>
import { host } from "../../const";
import { mapActions } from "vuex";
import timg from "../../assets/img/timg.gif";
export default {
  data() {
    return {
      host: host,
      content:"",
      initimg:timg//"/src/assets/img/timg.gif"
      
    };
  },
  props: ["item"],
  methods: {
    add(item) {
      item.n = 1;
      this.addCart(item);
      this.$toast("商品添加成功");
    },
    ...mapActions(["addCart"])
  }
};
</script>

<style lang="scss" scoped>
.item {
  padding-bottom: 20px;
  border: 1px solid #aaa;
  padding: 10px;
  margin: 10px;
  height: 2.5rem;
}
.item a{
    text-decoration: none;
}
.item p img {
  width: 2rem;
  height: 2rem;
  border: 1px solid gray;
  border-radius: 5px;
  float: right;
}
.item p {
  font-size: 0.4rem;
  line-height: 0.5rem;
}
.item p span {
  font-size: 0.5rem;
}
button {
  display:block;
  border: none;
  height: 0.5rem;
  background: #ddd;
  padding: 2px 5px;
  border: #aaa solid 1px;
  border-radius: 5px;
  margin-top: .5rem;
  text-decoration: none;
  margin-left: 2rem;
}
.msg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 3rem;
  height: 1rem;
  line-height: 1rem;
  text-align: center;
  background: skyblue;
  color: red;
  border-radius: 10px;
  padding: 10px;
}
b{
    display: block;
    margin-top: .2rem;
}
mark{
    display: block;
    margin-top: 5px;
    width: 1.6rem;
    text-align: center
}
</style>




