<template>
  <div class="box" @touchmove.prevent>
    <ul>
      <h1 style="color:orangered;text-align:center">{{type}}</h1>
      <li is="Item" v-for="item of arr" :key="item._id" :item="item"></li>
    </ul>
  </div>
</template>

<script>
import IScroll from "iscroll";
import { host } from "../../const";
import Item from "./Item.vue";
import lazy from "../../directives/lazyload";
export default {
  data() {
    return {
      type: "商品分类",
      arr: []
    };
  },
  components: {
    Item
  },
  mounted() {
    var self = this;
    this.type = this.$route.params.type;
    var page = 1;
    function loadmore() {
      let url =
        host +
        "/goods?_type=_type&_word=" +
        self.type +
        "&_limit=10&_page=" +
        page;
      self.$axios.get(url).then(res => {
        res.data.result.forEach(element => {
          self.arr.push(element);
        });
      });
    }
    loadmore();
    var boxH = document.querySelector(".box").offsetHeight;

    var myIScroll;
    setTimeout(function() {
      myIScroll = new IScroll(".box");
      myIScroll.on("scrollEnd", function() {
        if (
          this.y * -1 ==
          document.querySelector(".box>ul").offsetHeight -
            document.querySelector(".box").offsetHeight
        ) {
          page++;
          loadmore();
          self.$toast("loading")
        } else if (this.y == 0) {
          self.$toast("refresh")
        } else {
          //console.log(this.y)
        }
        lazy.show(boxH, this.y);
      });
      lazy.show(boxH, 0);
    }, 1000);
  }
};
</script>

<style lang="scss" scoped>
.box {
  overflow: hidden;
  background: rgb(173, 171, 171);
}
.box > ul {
  background: #b6e4c5;
}
li{
  color: wheat
}
</style>



