<template>
  <div>
    <Search></Search>
    <div class="swiper-container">
      <div class="swiper-wrapper">
        <div class="swiper-slide">
          <img src="../../assets/img/1.jpg" alt>
        </div>
        <div class="swiper-slide">
          <img src="../../assets/img/2.jpg" alt>
        </div>
        <div class="swiper-slide">
          <img src="../../assets/img/3.jpg" alt>
        </div>
        <div class="swiper-slide">
          <img src="../../assets/img/4.jpg" alt>
        </div>
        <div class="swiper-slide">
          <img src="../../assets/img/5.jpg" alt>
        </div>
      </div>
      <!-- 如果需要分页器 -->
      <div class="swiper-pagination"></div>

      <!-- 如果需要导航按钮 -->
      <div class="swiper-button-prev"></div>
      <div class="swiper-button-next"></div>
    </div>
    <div class="d1">
      <p>
        <span class="fa fa-home"></span>
        <router-link to="/goods/服装">服装</router-link>
      </p>
      <p>
        <span class="fa fa-home"></span>
        <router-link to="/goods/水果">水果</router-link>
      </p>
      <p>
        <span class="fa fa-home"></span>
        <router-link to="/goods/其他">其他</router-link>
      </p>
       <p>
        <span class="fa fa-home"></span>
        <router-link to="/goods/服装">服装</router-link>
      </p>
      <p>
        <span class="fa fa-bath"></span>
        <router-link to="/goods/水果">水果</router-link>
      </p>
      <p>
        <span class="fa fa-certificate"></span>
        <router-link to="/goods/其他">其他</router-link>
      </p>
    </div>
    <Content></Content>
  </div>
</template>

<script>
import Search from "../../main/search.vue";

import Content from "../../main/content.vue";
export default {
  beforeRouteLeave(to, from, next) {
    next();
    // console.log("执行了beforeRouteLeave.to",to)
  },
  data() {
    return {};
  },
  components: {
    Search,
    Content
  },
  mounted() {
    this.$eventbus.$emit("setTitle", "首页");
    var mySwiper = new Swiper(".swiper-container", {
      autoplay: {
        disableOnInteraction : false
      },
      loop: true, // 循环模式选项
      pagination:{
        clickable :true
      },
      

      // 如果需要分页器
      pagination: {
        el: ".swiper-pagination"
      },

      // 如果需要前进后退按钮
      navigation: {
        nextEl: ".swiper-button-next",
        prevEl: ".swiper-button-prev"
      }
    });
  }
};
</script>

<style lang="scss"  scoped>
.d1 {
  // height: 1.5rem;
  display: flex;
  justify-content: space-around;
  text-align: center;
  background: rgb(9, 61, 82);
  color: white;
  flex-wrap: wrap
}
.d1 p {
  margin: 10px;
  width:25%;
  height: 1rem;
  line-height: 1rem;
  display: flex;
  color: white;
  flex-direction: column;
}
.d1 p a{
   color: white;
}
.swiper-container {
  height: 3rem;
}
.swiper-container img {
  width: 100%;
  height: 100%;
}
</style>



