<template>
  <div class="ss">
      <div class="search">
        <input type="text" placeholder="search"><span class="fa fa-search"></span>
    </div>
  </div>
</template>

<script>
export default {};
</script>

<style leng="scss" scoped>
.ss{
  background: rgb(142, 181, 197);
  padding: 10px;
}
.search{
    margin: 0 auto;
    height: 0.6rem;
    width: 60%;
    border: 1px solid #666;
    display: flex;
    justify-content: space-between;
    border-radius: 5px;
    color: skyblue;
    background: #dac
}

:focus{
    outline: none
}
.search input{
    border: none;
    width: 80%;
    border-right: 1px solid #000;
    padding: 0 10px;
    font-size: 0.3rem;
    background: skyblue
}
.search span{
    width: 15%;
    line-height: 0.6rem;
    font-size: 20px;
    text-align: center;
    background: skyblue;
    color: #000
}

.img {
  width: 100%;
  height: 3rem;
  overflow: hidden;
}
.img img{
  width: 100%;
  height: 100%;
}
</style>



