<template>
  <div>
    <h1>注册</h1>
    <form @submit.prevent="registry">
      <p>
        <label>
         <span>
            用户名:
         </span>
          <input type="text" v-model="username.val" @blur="chkuser">
         
         
        </label>
      </p>
      <p>
        <label>
          <span>密码:</span>
          <input type="text" v-model="password.val1" @blur="chkupwd1">
        </label>
      </p>
      <p>
        <label>
         <span> 确认密码:</span>
          <input type="text" v-model="password.val2" @blur="chkupwd">
        </label>
      </p>
     <p>
        <button type="submit">注册</button>
     </p>
      
    </form>
  </div>
</template>

<script>
import {host} from "../../const"
export default {
    data(){
        return{
            username:{
                val:"",
            },
            password:{
                val1:"",
                val2:""
            },
            msg:"",
            flag:false
        }
    },
    methods:{
        chkuser(){
            if(this.username.val==""){
                 this.$toast("用户名不能为空")
            }
        },
        chkupwd1(){
            if(this.password.val1==""){
                this.$toast("密码不能为空")
            }
        },
        chkupwd(){
            if(this.password.val1!=this.password.val2){
               this.$toast("密码不一致");
                
            }
        },
        registry(){
           if(this.password.val1===this.password.val2&&this.username.val!=""){
               let url = host+"/registry";
               let data = "username="+this.username.val+"&password="+this.password.val1;
               this.$axios.post(url,data).then(res=>{
                    this.flag=true;
                    this.msg=res.data;
                    this.$toast(res.data);
                    setTimeout(() => {
                      this.flag=false;
                      if(res.data==="注册成功"){
                        this.$router.push({
                        path:"/mine/Login/"
                      });
                      }
                    }, 2000);     
               })
           }
        }
    }
};
</script>

<style lang="scss" scoped>
h1{
  text-align: center
}
form p{
  margin:0 30%;
}

input {
  margin: 10px;
  height: 0.5rem;
  border: 1px solid rgb(58, 161, 202);
}

button {
  width: 4rem;
  height: .8rem;
  line-height: .8rem;
  border: none;
  background: orange;
  color: white;
  font-size: .3rem;
    margin-left: -10%;
}
.msg{
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 2rem;
  height: 1rem;
  background: skyblue;
  color: red;
  border-radius: 10px;
  padding: 10px;
}
</style>



